.contact-form{
    padding: 0 3rem;
    display: flex;
    height: 80vh;
    margin-top: 8rem;
}
.cont-right{
    display: flex;
    justify-content: center;
    position: relative;
    flex: 1;
}
.cont-right >form{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    z-index: 1;
}
.cont-right .user{
    width: 20rem;
    height: 2rem;
    padding: 0.3rem;
    outline: none;
    border: 2px solid var(--orange);
    border-radius: 8px;
    font-size: 16px;
}
textarea{
    height: 4rem!important;
}

.send-button{
    height: 2.7rem;
    width: 6rem;
}
@media screen and (max-width:480px){
   .contact-form{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    margin-bottom: 250px;
   }
  
    }